.list {
  display: flex;
  justify-content: space-between;
  margin: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #ccc;
}

.list h3 {
  margin: 0 0 0.25rem 0;
}
.details{
    display: flex;
}
.description {
  font-style: italic;
}
.list a {
  text-decoration: none;
}
.price {
  margin-top: 0.25rem;
  font-weight: bold;
  color: #ad5502;
  font-size: 1.25rem;
  margin-right: 1rem;
}
.list button {
  font: inherit;
  cursor: pointer;
  background-color: #8a2b06;
  border: 1px solid #8a2b06;
  color: white;
  padding: 0.25rem 2rem;
  border-radius: 20px;
  font-weight: bold;
}

.list button:hover,
.list button:active {
  background-color: #641e03;
  border-color: #641e03;
}